async function createNewBlockSubByAddr(address, provider) {
    const result = await provider.subscribe("createUnreceivedBlockSubscriptionByAddress", address);
    return result;
}

async function getHashInfo(hash,provider) {
    const result = await provider.request("ledger_getAccountBlockByHash", hash);
    return result;
}

async function getTokenList(provider) {
    const result = await provider.request('contract_getTokenInfoList', 0, 1000);
    return result;
}

async function getTransactionHistory(address, provider, count){
    const result = await provider.request("ledger_getBlocksByAccAddr", address, 0 , count);
    return result;
}
export { createNewBlockSubByAddr, getTokenList, getHashInfo, getTransactionHistory };