import React from "react";

import styled from '@emotion/styled';
import Select from 'react-dropdown-select';
import styles from "./../styles.module.css"

const StyledSelect = styled(Select)`
  .react-dropdown-select-item.react-dropdown-select-item-selected,
  .react-dropdown-select-item.react-dropdown-select-item-active {
    background-color: white;
    color: #02298A;
  }
 
`;
const TransactionForm = ({state, inputToken, tokenId, setTokenId, options, allowMultipleTokens}) => {
  return (
      <div className={styles.form}>
          {allowMultipleTokens && (<label className={styles.allowMultipleTokensForm}>
          </label>)}
          <label style = {{display: state === 0 ? 'block' : 'none'}}> 
              {options.length > 0 ? (
                  <StyledSelect
                      className={styles.dropdown}
                      closeOnSelect={true}
                      clearOnSelect={true}
                      separator={true}
                      dropdownHandle={true}
                      options={options}
                      values={[options.find(opt => opt.tokenId === tokenId)]}
                      ref={inputToken}
                      onChange={(e) => { setTokenId(e[0]?.tokenId); }}
                  />
              ) : <p>Loading...</p>}
          </label>
      </div>)
};
export default TransactionForm;