import React from 'react'

import { VitePay } from './include/vitepay-module'
import './include/vitepay-module/styles.module.css'

import { sanitize } from "dompurify";

const paymentSuccess = (logs) => {
  console.log("Payment Success!", logs);
};

const paymentFailure = (logs) => {
  console.log("Payment Failure, See Logs", logs);
};

const paymentLogs = (logs) => {
  console.log("Payment Logs, Standard", logs);
};

const vpfwAmountDefault = sanitize(window.amountDefault, {ALLOWED_TAGS: []});
const vpfwTokenId = sanitize(window.tokenDefault, {ALLOWED_TAGS: []});
const vpfwDefaultMemo = sanitize(window.defaultMemo, {ALLOWED_TAGS: []});
const vpfwAllowMultipleTokens = sanitize(window.allowMultipleTokens=true, {ALLOWED_TAGS: []});
const vpfwshouldDisplayMemo = sanitize(window.shouldDisplayMemo=true, {ALLOWED_TAGS: []});
const vpfwRecipientAddress = sanitize(window.addressDefault, {ALLOWED_TAGS: []});
const vpfwNodeURL = sanitize(window.nodeURL, {ALLOWED_TAGS: []});
const vpfwHttpURL = sanitize(window.httpURL, {ALLOWED_TAGS: []});
const vpfwPaymentTimeout = sanitize(window.paymentTimeout, {ALLOWED_TAGS: []});

const App = () => {
  return (<div>
<VitePay
      amountDefault={vpfwAmountDefault}
      tokenDefault={vpfwTokenId}
      defaultMemo={vpfwDefaultMemo}
      displayToken={vpfwAllowMultipleTokens}
      displayMemo={vpfwshouldDisplayMemo}
      addressDefault={vpfwRecipientAddress}
      nodeURL={vpfwNodeURL}
      paymentTimeout={vpfwPaymentTimeout}
      buttonStyle={{
        "color": "#FFF",
        "backgroundColor": "#02298A",
        "fontSize": "18px",
        "textAlign": "center",
        "fontStyle": "normal",
        "borderRadius": "5px",
        "width": "200px",
        "borderWidth": "1px 1px 3px",
        "boxShadow": "0 -1px 0 rgba(255, 255, 255, 0.1) inset",
        "marginBottom": "10px",
        "height": "3em"
      }}
      onPaymentSuccess={paymentSuccess}
      onPaymentFailure={paymentFailure}
      onPaymentLogs={paymentLogs}
      httpURL={vpfwHttpURL}
    />
  </div>)
}

export default App