
// React related imports
import { QRCode } from 'react-qrcode-logo';
import React from "react";
import { useState, useEffect, useRef } from "react";
import ReactModal from 'react-modal';

// Encoding and big numbers
import { encode } from 'js-base64';
import Big from 'big.js';

// Vite related imports
import { ViteAPI } from '@vite/vitejs';
import { WS_RPC } from '@vite/vitejs-ws';
import HTTP_RPC from '@vite/vitejs-http';


// Local component imports
import { createNewBlockSubByAddr, getTokenList, getHashInfo, getTransactionHistory } from './components/client';
import TransactionForm from "./components/transForm";
import { transactionStatus } from "./components/transStatus";
import styles from "./styles.module.css"

export const TransactionCheck = ({ 
  nodeURL = "https://buidl.vite.net/gvite/http",
  recipientAddress,
  amount,
  memo,
  tokenId,
  checkPreviousTransactionsCount = 100 }) => {

  const [status, setStatus] = useState(false);

  useEffect(() => {
    async function fetchTransStatus() {
    let httpRPC = new HTTP_RPC(nodeURL);
    let provider = new ViteAPI(httpRPC, () => { return } );

    let transactions = await getTransactionHistory(recipientAddress, provider, checkPreviousTransactionsCount);
    transactions = transactions.filter(tx => tx.fromAddress !== tx.toAddress && tx.blockType === 4);
    if (transactions !== null && transactions.length > 0) {
      for (var i = 0; i < transactions.length; i++) {
        if (await transactionStatus(transactions[i], tokenId, amount, provider)) {
          setStatus(true);
          break;
        }
      }
    }
  }
  fetchTransStatus();
},[amount, checkPreviousTransactionsCount, memo, nodeURL, recipientAddress, tokenId]);

  return status === true ? ("Transaction Found") : ("Transaction Not Found");

};

export const VitePay = ({
  amountDefault,
  tokenDefault,
  addressDefault,
  nodeURL,
  httpURL,
  defaultMemo,
  paymentTimeout,
  allowMultipleTokens,
  displayMemo,
  buttonStyle,
  onPaymentSuccess,
  onPaymentFailure,
  onPaymentLogs,
  checkPreviousTransactionsCount = 500 }) => {


  const inputMemo = useRef(defaultMemo);
  const inputAmount = useRef(amountDefault);
  const inputToken = useRef(tokenDefault);

  const [address, setAddress] = useState(addressDefault);
  const [tokenId, setTokenId] = useState(tokenDefault);
  const [memo, setMemo] = useState(defaultMemo);
  const [amount, setAmount] = useState(amountDefault);

  const [options, setOptions] = useState([]);
  const [qr, setQR] = useState(`vite:${address}?tti=${tokenId}&amount=${amount}&data=${encode(memo).replaceAll("=", "")}`);

  const [state, setState] = useState(0);
  const [timer, setTimer] = useState(parseInt(paymentTimeout));



  useEffect(() => {
    async function checkPaymentStatus() {
      let WS_service = new WS_RPC(nodeURL);
      let provider = new ViteAPI(WS_service);
      // set dropdown from the getToken
      const token = await getTokenList(provider);

      token.tokenInfoList.forEach((value, index) => {
        value.label = value.tokenName;
        value.key = index;
      });
      setOptions(token.tokenInfoList);

      const event = await createNewBlockSubByAddr(address, provider);
      event.on(async (result) => {

        const hashAddress = result[0].hash;
        const txInfo = await getHashInfo(hashAddress, provider);
      
        if (inputAmount.current && inputToken.current)
        {
          // make sure if the transaction is valid or not. 
          let divider = `1e+${txInfo.tokenInfo.decimals}`
          let amountTx = (new Big(`${txInfo.amount}`)).div(Big(divider));

          if (parseInt(amountTx) === parseInt(amount) && txInfo.tokenId === tokenId && txInfo.timestamp > (new Date().getTime()/1000 - parseInt(paymentTimeout)))
          {
            setState(2);
            onPaymentSuccess(txInfo);
          }
          else if (timer < 3)
          {
            setState(-1);
            onPaymentFailure(txInfo);
          }
          else
          {
            onPaymentLogs(txInfo);
            setState(1);
          }
        }   
      });
    }
    checkPaymentStatus();

  }, [address, amount, tokenId, allowMultipleTokens, displayMemo, nodeURL, onPaymentFailure, onPaymentLogs, onPaymentSuccess, timer, paymentTimeout]);

  useEffect(() => {
    if (timer > 0) {
      setTimeout(() => setTimer(timer - 1), 1000);
    } else {
      setState(-1);
    }
  }, [timer]);


  // change QR when variables are changed
  useEffect(() => {
    async function setQRAsync() {
      setQR(`vite:${address}?tti=${tokenId}&amount=${amount}&data=${encode(memo).replaceAll("=", "")}`);
    }
    setQRAsync();
  }, [address, tokenId, memo, amount]);


  async function checkAPIStatus(e, tokenId, memo, amount) {
    e.preventDefault();

    let httpRPC = new HTTP_RPC(httpURL);
    let provider = new ViteAPI(httpRPC, () => {
      return
    });

    let transactions = await getTransactionHistory(address, provider, checkPreviousTransactionsCount);
    transactions = transactions.filter(tx => tx.fromAddress !== tx.toAddress && tx.blockType === 4);
    let statusTransaction = false;
    if (transactions !== null && transactions.length > 0) {
      for (var i = 0; i < transactions.length; i++) {
        if (await transactionStatus(transactions[i], tokenId, amount, provider)) {
          statusTransaction = true;
          setState(2);
          break;
        }
      }
    }
    return statusTransaction;
  }

  return (
    <div className={styles.App}>
      <ReactModal className={styles.modal} isOpen={true}>

        <form className={styles.vitepay}>

          {state === 0 && <p>Transaction will expire in {Math.floor(timer / 60)}{"m "}{(timer - Math.floor(timer / 60) * 60)}{"s"}</p>}
          {(state === 1  || state === 2) && <p>Transaction is Complete</p>}
          {state === -1 && <p>Transaction has failed</p>}
          <QRCode size="200" className={styles.qrCode} logoImage="/vitelabs-removebg.jpg" value={qr} />
          {(state === 0  || state === 1) && (
            <TransactionForm state={state} inputMemo={inputMemo} inputAmount={inputAmount} inputToken={inputToken} checkStatus={checkAPIStatus} displayMemo={displayMemo} allowMultipleTokens={allowMultipleTokens} memo={memo} setMemo={setMemo} address={address} setAddress={setAddress} tokenId={tokenId} setTokenId={setTokenId} amount={amount} setAmount={setAmount} options={options} />
          )}
        </form>

      </ReactModal>

    </div>

  );
}